@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Block,.Schedule,.Header,.AddButton{font-family:Inter, sans-serif;display:flex;flex-direction:row}.Schedule{justify-content:space-between;flex-wrap:wrap}.Schedule>.Info{padding:1.5rem;flex-basis:80%;font-size:1.5rem;display:flex;flex-direction:column}.Schedule>.Info>.MainBar>.Title{padding-right:0.3rem;font-weight:700;display:inline}.Schedule>.Info>.MainBar>.Countdown{font-weight:300;display:inline}.Schedule>.Info>.InfoBar{padding-top:0.4rem;font-size:1.0rem}.Schedule>.Info>.InfoBar>.Date{padding-right:0.3rem;display:inline}.Schedule>.Info>.InfoBar>.Date::after{content:" •"}.Schedule>.Info>.InfoBar>.Description{display:inline}.Schedule>.EditSection{padding:1.5rem;background-color:rgba(0,0,0,0.05);width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}.Schedule>.EditSection .Field{display:flex;flex-direction:column;flex:1 1;margin:0 0.3rem}.Schedule>.EditSection .Field .FieldTitle{font-weight:700}.Schedule>.EditSection .Field .FieldInput{outline:none;border:1px solid black;background-color:inherit;font-family:inherit;font-size:inherit;padding:10px}.Schedule>.EditSection .Field .FieldInput:hover{outline:none}.Schedule>.EditSection .Field button.FieldInput:hover{background-color:#00b276}.Schedule>.EditSection .Field button.FieldInput--red{color:white;background-color:firebrick}.Schedule>.EditSection .Field button.FieldInput--red:hover{color:white;background-color:#871a1a}.Header{margin:1.5rem;justify-content:space-between;flex-wrap:wrap}.Header>.Main{font-weight:700;display:inline}.Header>.Description{font-weight:300;display:inline}.AddButton{padding:1.5rem;text-align:center;justify-content:center;flex-wrap:wrap;font-weight:700;color:white;background-color:#7fb1b3;-webkit-user-select:none;-ms-user-select:none;user-select:none}.AddButton:hover{background-color:cadetblue}

